@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@keyframes blink {
  from, 50% {
    opacity: 1;
  }
  25%, 75% {
    opacity: 0;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes zoom-in {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes spin {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}
@keyframes roll {
  from {
    transform: translate(-100%) rotate(0turn);
  }
  to {
    transform: translate(100%) rotate(10turn);
  }
}
:root {
  --progress: 0;
}

.loader-g {
  --loader-g-size: 50vmin;
  --loader-g-inner-size: 10%;
  --loader-g-inner-bg: #ddd;
  --loader-g-bg: linear-gradient(#14ffe9, #ffeb3b, #ff00e0);
  --loader-g-inner-text: #000;
  position: relative;
  width: var(--loader-g-size);
  height: var(--loader-g-size);
  font-size: calc(var(--loader-g-size) * 0.375);
  margin: calc(var(--loader-g-size) / 6);
  animation: spin 0.5s linear infinite;
  box-shadow: 0 0 16px 16px rgba(0, 0, 0, 0.3);
}
.loader-g, .loader-g span {
  border-radius: 50%;
  background: var(--loader-g-bg);
}
.loader-g span {
  width: 100%;
  height: 100%;
  position: absolute;
}
.loader-g span:nth-child(1) {
  filter: blur(5px);
}
.loader-g span:nth-child(2) {
  filter: blur(10px);
}
.loader-g span:nth-child(3) {
  filter: blur(25px);
}
.loader-g span:nth-child(4) {
  filter: blur(50px);
}
.loader-g::after {
  content: "";
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: var(--loader-g-inner-size, 10%);
  left: var(--loader-g-inner-size, 10%);
  width: calc(100% - 2 * var(--loader-g-inner-size, 10%));
  height: calc(100% - 2 * var(--loader-g-inner-size, 10%));
  border-radius: 50%;
  background: var(--loader-g-inner-bg);
  box-shadow: inset 0 0 16px 16px rgba(0, 0, 0, 0.3);
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
  animation: spin 0.5s linear infinite reverse;
}
.loader-g[data-percent]::after {
  content: attr(data-percent) "%";
  color: var(--loader-g-inner-text);
}
.loader-g.fast, .loader-g.fast::after {
  animation-duration: 0.25s;
}
.loader-g.faster, .loader-g.faster::after {
  animation-duration: 0.15s;
}
.loader-g.slow, .loader-g.slow::after {
  animation-duration: 0.75s;
}
.loader-g.slower, .loader-g.slower::after {
  animation-duration: 1s;
}
.loader-g.x2 {
  --loader-g-size: 12rem;
}
.loader-g.x3 {
  --loader-g-size: 16rem;
}
.loader-g.x4 {
  --loader-g-size: 20rem;
}
.loader-g[data-percent="100"] {
  animation-duration: 5s;
}
.loader-g[data-percent="100"]::after {
  animation-duration: 5s;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
v2.0 | 20110126
License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, main, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  background: #181818;
}

:root {
  /* Spinner */
  --spinner-size: 64px;
  --spinner-border-opacity: 0.867;
  --spinner-rotate-opacity: 1;
  --spinner-border-rgb: 221, 221, 221;
  --spinner-border-size: 12px;
  --spinner-rotate-rgb: 10, 255, 237;
  --spinner-border-color: rgba(var(--spinner-border-rgb), var(--spinner-border-opacity));
  --spinner-border-radius: 50%;
  --spinner-rotate-color: rgba(var(--spinner-rotate-rgb), var(--spinner-rotate-opacity));
}

.spinner {
  animation: spin 3s linear infinite;
  width: var(--spinner-size);
  height: var(--spinner-size);
  border-radius: var(--spinner-border-radius);
  border: var(--spinner-border-size) solid var(--spinner-border-color);
  display: flex;
  justify-content: center;
  align-items: center;
  transform-origin: center center;
  position: relative;
}
.spinner, .spinner.x1 {
  border-top-color: var(--spinner-rotate-color);
}
.spinner.x2, .spinner.x3 {
  border-left-color: var(--spinner-rotate-color);
}
.spinner.x3 {
  border-bottom-color: var(--spinner-rotate-color);
}
.spinner.fast {
  animation-duration: 2s;
}
.spinner.faster {
  animation-duration: 1s;
}
.spinner.reverse {
  animation-direction: reverse;
}
.spinner.roll {
  animation-name: roll;
}

.loader {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loader .label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

