.progress {
  --progress-height: 2rem;
  --progress-font-size: calc(var(2rem) * 0.75);
  --progress-bg: rgba(221, 221, 221, 0.867);
  --progress-color: #dddddd;
  --progress-bar-color: #dddddd;
  --progress-loading: #212529;
  --progress-bar-bg: #0d6efd;
  --progress-bar-complete-bg: #198754;
  --progress-bar-error-bg: #dc3545;
  --progress-border-radius: 24px;
  --progress-bar-border-radius: 24px;
  --progress-border-size: 1px;
  --progress-border-color: rgba(0, 0, 0, 0.3);
  position: relative;
  display: flex;
  height: var(--progress-height);
  overflow: hidden;
  font-size: var(--progress-font-size);
  background: var(--progress-bg);
  border-radius: var(--progress-border-radius);
  border: var(--progress-border-size) solid var(--progress-border-color);
  margin-top: calc(var(--progress-height) / 4);
  margin-bottom: calc(var(--progress-height) / 4);
  color: var(--progress-loading);
  position: relative;
  pointer-events: none;
  user-select: none;
  box-shadow: 0 0 4px 2px var(--progress-border-color);
}
.progress .progress-bar {
  width: calc(var(--progress, 0) * 1%);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  background: var(--progress-bar-bg);
  transition: width 0.25s ease-out, background 1.5s ease 1.5s;
  border-radius: var(--progress-bar-border-radius);
  height: 100%;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  color: var(--progress-bar-color);
  box-shadow: inset 0 0 var(--progress-height) 12px var(--progress-border-color);
}
.progress .progress-bar:not(:empty) {
  padding-right: 1%;
}
.progress .progress-bar:not(:empty)::after {
  content: "%";
}
.progress::before {
  z-index: 1;
  position: absolute;
  top: var(--progress-border-size);
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.progress[data-percent]::before {
  content: attr(data-percent) "%";
}
.progress[data-load]::before {
  content: attr(data-load);
}
.progress[data-percent="100"] .progress-bar, .progress.complete .progress-bar {
  background: var(--progress-bar-complete-bg);
}
.progress[data-percent="100"][data-complete]::before, .progress.complete[data-complete]::before {
  content: attr(data-complete);
  color: var(--progress-color);
}
.progress[data-percent="100"]::before, .progress.complete::before {
  z-index: 3;
  animation: blink 1.5s ease infinite reverse both;
}
.progress.error .progress-bar {
  background: var(--progress-bar-error-bg);
}
