[hidden],
.overlay:not([data-open=true]) .dialog,
.overlay:not([data-open=true]) {
  visibility: hidden !important;
  position: fixed !important;
  z-index: -1 !important;
  pointer-events: none !important;
  opacity: 0 !important;
}

.overlay {
  --overlay-background: rgba(8, 25, 43, 0.85);
  --overlay-close-background: rgba(0, 0, 0, 0);
  --overlay-close-background-hover: rgba(255, 255, 255, 0.2);
  --overlay-close-color: #fff;
  --overlay-close-color-hover: #fff;
  position: fixed;
  z-index: 2147483630;
  inset: 0;
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  backface-visibility: hidden;
  background: var(--overlay-background);
  box-shadow: inset 0 0 16px 16px var(--overlay-background);
  width: 100vw;
  min-height: 100vh;
  overflow-y: scroll;
  transition: opacity 2.5s cubic-bezier(1, 0, 0, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;
  flex-wrap: wrap;
}
.overlay__top {
  justify-content: flex-start;
}
.overlay__bottom {
  justify-content: flex-end;
}
.overlay--close {
  animation: fade-in 0.25s linear 0.5s both;
  position: fixed;
  right: 1vw;
  top: 1vh;
  z-index: 2147483632;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  line-height: 32px;
  font-weight: 400;
  width: 40px;
  height: 40px;
  cursor: pointer;
  user-select: none;
  background: var(--overlay-close-background);
  color: var(--overlay-close-color);
  border-radius: 3px;
  transition: background 0.25s linear, color 0.25s linear;
}
.overlay--close:hover {
  background: var(--overlay-close-background-hover);
  color: var(--overlay-close-color-hover);
}
.overlay--close:empty:before {
  content: "";
}
.overlay:not([data-open=true]) {
  transform: scale(0);
}
.overlay .dialog {
  --dialog-width: 80vw;
  --dialog-height: 60vh;
  --dialog-color: #333;
  --dialog-background: #fff;
  --dialog-border: 3 px solid rgba(0, 0, 0, 0.3);
  --dialog-border-radius: 8px;
  --dialog-margin-x: auto;
  --dialog-margin-y: 32px;
  animation: zoom-in 0.25s ease-out 0.5s both;
  color: var(--dialog-color);
  background: var(--dialog-background);
  width: var(--dialog-width);
  min-height: var(--dialog-height);
  border: var(--dialog-border);
  border-radius: var(--dialog-border-radius);
  margin: var(--dialog-margin-y) var(--dialog-margin-x);
  box-shadow: 4px 4px 16px 16px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow-y: auto;
}
.progress-line {
  background: linear-gradient(-225deg, #02d7f2 0%, #f2e900 29%, #007aff 67%, #ff1111 100%);
  top: 0;
  left: 0;
  right: 50%;
  height: 3px;
  width: calc(var(--progress, 0) * 1%);
  position: fixed;
  transition: background 2s linear infinite both, width 0.25s ease both, transform 0.25s ease both;
}
.progress-line.grow-center {
  transition: background 2s linear, transform 0.25s ease-out;
  left: 50%;
  transform: translateX(-50%);
}

.fluo {
  --fluo-outer-size: 10rem;
  --fluo-wheel-size: 10%;
  --fluo-inner-bg: #000;
  --fluo-text-color: #ddd;
  --fluo-wheel-gradient: linear-gradient(#14ffe9, #ffeb3b, #ff00e0);
  position: relative;
  width: var(--fluo-outer-size);
  height: var(--fluo-outer-size);
  font-size: calc(var(--fluo-outer-size) * 0.375);
  margin: calc(var(--fluo-outer-size) / 6);
  animation: spin 0.5s linear infinite;
  box-shadow: 0 0 16px 16px rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  background: var(--fluo-wheel-gradient);
  color: var(--fluo-text-color);
}
.fluo > * {
  border-radius: 50%;
  background: var(--fluo-wheel-gradient);
  width: 100%;
  height: 100%;
  position: absolute;
}
.fluo > *:nth-child(1) {
  filter: blur(5px);
}
.fluo > *:nth-child(2) {
  filter: blur(10px);
}
.fluo > *:nth-child(3) {
  filter: blur(25px);
}
.fluo > *:nth-child(4) {
  filter: blur(50px);
}
.fluo::after {
  content: "";
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  inset: var(--fluo-wheel-size);
  width: calc(100% - 2 * var(--fluo-wheel-size));
  height: calc(100% - 2 * var(--fluo-wheel-size));
  border-radius: 50%;
  background: var(--fluo-inner-bg);
  box-shadow: inset 0 0 16px 16px rgba(0, 0, 0, 0.3);
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
  animation: spin 0.5s linear infinite reverse;
}
.fluo.fast, .fluo::after.fast {
  animation-duration: 0.25s;
}
.fluo.faster, .fluo::after.faster {
  animation-duration: 0.15s;
}
.fluo.slow, .fluo::after.slow {
  animation-duration: 0.75s;
}
.fluo.slower, .fluo::after.slower {
  animation-duration: 1s;
}
.fluo[data-percent]::after {
  content: attr(data-percent) "%";
}
.fluo[data-percent="100"], .fluo.complete {
  animation-duration: 5s;
}
.fluo[data-percent="100"]::after, .fluo.complete::after {
  animation-duration: 5s;
}

